<template>
  <div>
    <div class="headline">货款详情
      <div class="goback" @click="back">
        <img src="/static/img/back.png" alt="">         
      </div>
    </div>

    <div class="box1">
      <div class="img-box">
        <img v-lazy="detail.thumb" alt="">
      </div>
      <div>
        <p>{{detail.title}}</p>
        <p class="light">累计<span>{{detail.success_num}}</span>人成功贷款</p>
      </div>
    </div>

    <div class="box2">
      <div class="col-3">
        <img src="/static/img/dk.png" alt="">        
        <p>贷款额度</p>
        <p class="light">{{detail.price_min}}-{{detail.price_max}}</p>      
      </div>   
      <div class="col-3">
        <img src="/static/img/qx.png" alt="">        
        <p>期限额度</p>
        <p class="light">{{detail.date}}</p>      
      </div>  
      <div class="col-3">
        <img src="/static/img/rll.png" alt="">        
        <p>日利率低至</p>
        <p class="light">{{detail.interest}}</p>      
      </div>                                      
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      detail: Object
    },
    methods: {
      back(){
        this.$router.go(-1);//返回上一层
      }
    }    
  }
</script>


<style  lang="scss" scoped>
  .headline {
    width: auto;
    height: 38px;
    text-align: center;
    font-size:15px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(71,76,69,1);
    line-height:38px;
    position: relative;
    background:rgba(255,255,255,1);
    border-bottom:1px solid #efeeee;

    .goback {
      width: 15px;
      height: 15px;
      padding: 13px;
      position: absolute;
      left: 5px;
      top: 0px;
      z-index: 88;
      display: flex;
      justify-content: center;
      align-items: center;      

      img{
        width: 15px;
        height: 15px;        
      }
    }   
  }

  .light{
    font-size:14px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(150,150,150,1);
    line-height:20px;
  }

  .box1{
    width: auto;
    height: 73px;
    display: flex;
    align-items: center;
    background:rgba(255,255,255,1);
    border-bottom:1px solid #efeeee; 

    .img-box{
      width: 53px;
      height: 53px;
      margin: 0px 16px;

      img{
        width: 53px;
        height: 53px;       
      }
    } 

    p{
      span{
        color:#EA402F;
      }
    }  
  }

  .box2{
    width: auto;
    height: 117px;
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    background:rgba(255,255,255,1);

    .col-3{
      flex:0 0 33.33333%;
      height: 78px;
      display: flex;
      flex-direction: column ;
      justify-content: center;
      align-items: center;

      img{
        width:42px;
        height:42px;          
      }

    }
  }
</style>



