<template>
  <div>
    <Introduction :detail="detail"/> 
    <Condition :detail="detail"/> 
    <Payment :detail="detail"/> 
    <div class="apply" @click="account">立即申请</div>    
  </div>
</template>

<script>
  import Introduction from '@/pages/details/Introduction.vue'
  import Condition from '@/pages/details/Condition.vue'
  import Payment from '@/pages/details/Payment.vue'

  export default {
    components: {
      Introduction,
      Condition,
      Payment
    },    
    data () {
      return { 
        id: this.$route.params.id,
        detail:{}                 
      }
    },
    created() {
      this.getData()
    },           
    methods: {
      getData() {
        this.$axios.get('getOneProduct/' + this.$route.params.id,{
        })
        .then(res=>{
          if (res.data.code === 200) {
            this.detail = res.data.data
          } else {
            console.log('**********code is not 200 ***********')
          }
        })
        .catch(err=>{
          console.log(err)
        })
      },
      account(){
        const {id, detail} = this

        var qs = require('qs')
        this.$axios.post('Record',
          qs.stringify({ 
          "product": `${detail.title}_${id}`
          })
        )
        .then(res=>{
          window.open(`${detail.application_url}`,"_blank") 
        })
        .catch(err=>{
          console.log(err)
        })   
          
      }     
    }
  }
</script>


<style  lang="scss" scoped>
  .apply{
    width: auto;
    height: 40px;
    margin: 20px 17px 0px 17px;
    background:rgba(51,154,246,1);
    border-radius:8px;

    font-size:18px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(255,255,255,1);
    line-height:38px;

    display: flex;
    justify-content: center;
    align-items: center;

  } 
</style>



