<template>
  <div> 
    <div v-if="detail.pay_zfb === '1' ||
    detail.pay_wx === '1' ||
    detail.pay_BankCard === '1' ||
    detail.pay_Money === '1' ||
    detail.pay_web === '1'">           
      <div class="divide">            
        <p>付款方式</p>
      </div>
    </div>

    <div v-if="detail.pay_zfb === '1'">
      <div class="pay">
        <div class="pay-sub">
          <div class="zfb">
            <img src="/static/img/zfb.png" alt=""> 
          </div>
          <p>支付宝</p>       
        </div>
        <div><img class="toggle" src="/static/img/go.png" alt=""> </div>
      </div>
    </div>

    <div v-if="detail.pay_wx === '1'">
      <div class="pay">
        <div class="pay-sub">
          <div class="zfb">
            <img src="/static/img/wx.png" alt=""> 
          </div>
          <p>微信支付</p>       
        </div>
        <div><img class="toggle" src="/static/img/go.png" alt=""> </div>
      </div>
    </div>

    <div v-if="detail.pay_BankCard === '1'">
      <div class="pay">
        <div class="pay-sub">
          <div class="zfb">
            <img src="/static/img/yl.png" alt=""> 
          </div>
          <p>银行卡支付</p>       
        </div>
        <div><img class="toggle" src="/static/img/go.png" alt=""> </div>
      </div>  
    </div>

    <div v-if="detail.pay_Money === '1'">
      <div class="pay">
        <div class="pay-sub">
          <div class="zfb">
            <img src="/static/img/xj.png" alt=""> 
          </div>
          <p>现金支付</p>       
        </div>
        <div><img class="toggle" src="/static/img/go.png" alt=""> </div>
      </div>  
    </div>

    <div v-if="detail.pay_web === '1'">
      <div class="pay">
        <div class="pay-sub">
          <div class="zfb">
            <img src="/static/img/gw.png" alt=""> 
          </div>
          <p>官网支付</p>       
        </div>
        <div><img class="toggle" src="/static/img/go.png" alt=""> </div>
      </div>  
    </div>

  </div>
</template>

<script>
  export default {
    props: {
      detail: Object
    }
  }
</script>


<style  lang="scss" scoped>
  .divide{    
    width: auto;
    height: 20px; 
    padding: 13px 16px;
  }  

  .pay{
    width: auto;
    height: 55px;     
    background:rgba(255,255,255,1); 
    border-bottom:1px solid #efeeee; 
    display: flex;
    justify-content: space-between;
    align-items: center;

    .pay-sub{
      display: flex;
      align-items: center;  

      .zfb{
        width: 40px;
        height: 40px; 
        margin: 0px 15px;

        img{
          width: 40px;
          height: 40px;           
        }         
      }   
    }

    .toggle{
      width: 26px;
      height: 26px; 
      margin-right: 15px;        
    }
  } 
</style>



