<template>   
  <div>
    <div class="box3">
      <div class="title">
        <p>申请条件</p>
      </div>
      <div>
        <p class="light">{{detail.condition}}</p>
      </div>
    </div>

    <div class="box3">
      <div class="title">
        <p>审核说明</p>
      </div>
      <div>
        <p class="light">{{detail.check}}</p>                       
      </div>
    </div>

    <div v-if="detail.phone_num === null"></div>
    <div v-else-if="detail.phone_num === ''"></div>
    <div v-else>
      <div class="box3">
        <div class="title">
          <p>服务热线</p>
        </div>
        <div>
          <p class="light">{{detail.phone_num}}</p>                       
        </div>
      </div>
    </div>

    <div v-if="detail.introduction === null"></div>
    <div v-else-if="detail.introduction === ''"></div>            
    <div v-else>
      <div class="box3">
        <div class="title">
          <p>公司简介</p>
        </div>
        <div>
          <p class="light">{{detail.introduction}}</p>                       
        </div>
      </div>        
    </div>

    <div v-if="detail.address === null"></div>
    <div v-else-if="detail.address === ''"></div>            
    <div v-else>
      <div class="box3">
        <div class="title">
          <p>支持地区</p>
        </div>
        <div>
          <p class="light">{{detail.address}}</p>                       
        </div>
      </div>        
    </div>

  </div>
</template>

<script>
  export default {
    props: {
      detail: Object
    }
  }
</script>


<style  lang="scss" scoped>
  .light{
    font-size:14px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(150,150,150,1);
    line-height:20px;
  }

  .box3{
    margin-top: 8px;
    background:rgba(255,255,255,1);

    div{
      padding: 7px 16px;      
    }

    .title{
      border-bottom:1px solid #efeeee; 
    }
  }
</style>



